.App {
  font-family: 'Nunito';
}

.nav-custom {
  padding-bottom: 15px;
  

  .brand {
    align-items: center;
    background-color: #0800ff;
    border-bottom: 2px solid #fff;
    display: flex;
    padding: 20px 0;

    img {
      width: 150px;
      margin: 0 3em;
    }

    h1 {
      color: #fff;
      font-size: 4em;
      font-weight: 900;
      margin: 0;
    }
  }

  .links {
    padding: 20px 0;
    background-color: #0800ff;

    ul {
      display: flex;
      list-style: none;
      margin: 0;
  
      li {
        margin: 0 5px;
  
        a {
          color: #fff;
          padding: 10px;
        }
  
        .active {
          background-color: #fff;
          border-radius: 5px;
          color: #0800ff;
        }
      }
    }
  }
}

.bg-paint {
  background-image: url('../images/paint.png');
  background-size: cover;
  height: 1400px;

  p {
    font-size: 5em;
  }

  .red {
    display: inline-block;
    left: 152px;
    position: relative;
    top: 63px;
  }

  .orange {
    display: inline-block;
    left: -41px;
    position: relative;
    top: 242px;
  }
  
}

.bg {
  height: 100%;
  background: url('../images/tool-bg1.png');
  background-size: contain;
  background-repeat: repeat;
}

.container {
  h1 {
    font-size: 3em;
    font-weight: 700;
    text-align: center;
  }
}

.food {
  img {
    margin-bottom: 20px;
  }
}

.team {
  img {
    margin-bottom: 20px;
    max-height: 400px;
    max-width: 100%;
  }
}